import React, { Component } from 'react';

import io from 'socket.io-client';
import './App.css';
import { TextField, Icon, InputAdornment, Button } from '@material-ui/core'
import ResultCard from './components/ResultCard'
import {Api} from './helpers/api'
import Loader from './components/loader'

const ISLOCAL = true
const remoteIp = 'http://18.207.30.129'
function DownloadProgress(props){
  const query = props.query
  const downloadState = props.downloadState
    if (downloadState === 1) {
      return <Loader text = {'Produzindo Documentos...'}></Loader>
      
    } else if (downloadState === 2) {
     return <a  href={"/data/" + query + '.zip'} download={"/data/" + 
      query + '.zip'}>Click to Download </a> 
    } else{
     return null
    }
}
class App extends Component {
  constructor(props) {
    super(props)
    this.state = { 
      query: "",
      results: null,
      docsSize: null,
      download: 0,
      loading: false
    }
    // this.socket = io('http://127.0.0.1/socket.io');
    this.socket = io('/', {
      secure: true,
      rejectUnauthorized: false
      // path: 'chat/socket.io'
    });

  }

  componentWillMount() {
    // this.socket.on('alerta', (dado) => {
    //   console.log(dado)
    // })

    this.socket.on('count', (data) => {
      console.log(data)
      if (data){
        this.setState({docsSize: data[0]['count(id)']})
      }
      
      this.setState({loading:false})

    })
    this.socket.on('news', (data) => {
      console.log(data)
      // this.socket.emit('clienttest','penisao')
    })
    
  
    this.socket.on('preview', (data) => {
      console.log('First Five docs arrived!')
      console.log(data)
      this.setState({ results: data })
      this.getRowCount()

    })

    this.socket.on('download', (dado) => {
      // console.log(dado)
      this.setState({download: 2})
    })
    this.socket.on('teste', (dado) => {
      console.log(dado)
      // this.setState({download: true})
    })
    this.socket.on('individualId', (dado) => {
      console.log(dado)
    })
    this.socket.on('client', (dado) => {
      console.log(dado)
    })
    
  }

  componentDidMount() {
    Api.query('esse é o texto da query', (resultado) => {
      console.log(resultado)
    })
  }


  onQuerySubmit = async () => {
    this.setState({loading:true,download:0})
    if (this.state.query === '') {
      return false
    }


    this.socket.emit('/q',this.state.query)
    this.getRowCount()

   
  }

  killQuery = async () => {

    let socketParams = {
      killQuery: true,
      counting: this.state.loading,
      buildingArchives: this.state.download
    }
    this.socket.emit('/killQuery',socketParams)


  }

  getRowCount = async () => {
    if (this.state.query === '') {
      return false
    }

    this.socket.emit('/count',this.state.query)

  }


  onDownload = async () => {
    this.setState({download:1})
    if (this.state.query === '') {
      return false
    }


    this.socket.emit('/download',this.state.query)

  }

  onChildClicked = (id) => {
    console.log('O ID ' + id + ' CLICOU' )
  }

  render() {

    return (
      <div className="App">
        <header className="App-header">
          <h1 className="App-title"> JurisMagic </h1>
        </header>


        <TextField
          id="search"
          label="Search field"
          type="search"
          margin="normal"
          value={this.state.query}
          fullWidth={true}
          style={{ width: '80%' }}
          onChange={(ev) => {
            this.setState({ query: ev.target.value })
          }}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <Icon>search</Icon>
              </InputAdornment>
            ),
          }}
        />
        <br />
        <div style={{width:'100%',display:"flex",justifyContent:'center'}}>
          <Button variant="contained" style={{width:'22%',marginRight:"2%"}} color="primary" onClick={this.onQuerySubmit} >
            Pesquisar
          <Icon style={{ marginLeft: 5 }}>send</Icon>
          </Button>
          <Button variant="contained" style={{width:'22%',marginRight:"2%"}} color="primary" onClick={this.killQuery} >
            Cancelar Pesquisa
          <Icon style={{ marginLeft: 5 }}>cancelr</Icon>
          </Button>
          <Button className="Download-Button" style={{width:'22%',marginRight:"2%"}} variant="contained" color="primary" onClick={this.onDownload}>
            Produzir Arquivos
          <Icon style={{ marginLeft: 5 }} >archive</Icon>
          </Button>
        </div>
        <br></br>
        <DownloadProgress style={{marginTop:'5%'}} downloadState = {this.state.download} query = {this.state.query}></DownloadProgress>
        {this.state.loading?<Loader text = {'Contando Documentos...'}></Loader> : null}
        {this.state.docsSize && this.state.results && this.state.query !== "" ? <p>Foram encontrados {this.state.docsSize} documentos </p> : null}

        {this.state.results
          ? this.state.results.map((r, i) => <ResultCard onClick={this.onChildClicked} key={'result' + i} {...r} />)
          : null
        }
 
        {this.state.results instanceof Array && this.state.results.length === 0 ? <p> Nenhum documento foi encontrado. </p> : null}
      </div>
    );
  }
}

export default App;



